@use 'base/mixins' as m;
@use 'base/variables' as v;

.galeria{
  padding: 5rem 0;
  h3{
    text-align: center;
    color: v.$blanco;
  }
}

.galeria-imagenes{
  @include m.resetear-lista;
  @include m.grid(2, 2rem);

  @include m.telefono {
    @include m.grid(3, 2rem);
  }
  
  @include m.tablet {
    @include m.grid(4, 2rem);
  }

  @include m.desktop {
    @include m.grid(6, 2rem);
  }

}

.overlay{ //Clase creada en app.js
  position: fixed; //Posicion fija
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  width: 100%;
  height: 100%;

  display: grid;
  place-items: center;

  background-color: rgb(0 0 0 /.8); //Para la transparencia

  picture{ //Etiqueta creada en app.js
    display: block;
    max-width: 80rem;
  }
}


.btn-cerrar{ //Clase creada en app.js
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: v.$blanco;
  color: v.$negro;
  display: grid;
  place-items: center;
  font-weight: 700;
}